.item{

    width: 350px;

}

.item p{
    margin: 6px 0px;
}

.item-prices{

    display: flex;
    gap: 20px;

}

.item-price-new{
    color:#374151;
     font-size: 18px;
    font-weight: 600;
}

.item-price-old{
    color: #8c8c8c;
    font-size: 18px;
    font-weight: 500;
    text-decoration: line-through;
}

.item:hover{
   transform: scale(1.05);  /* This property scales the element to 105% of its original size, making it appear slightly larger. */

    transition: 0.6s; /* This property ensures that the transformation occurs smoothly over 0.6 seconds.*/
}
