.navbar
{
    display: flex;
    justify-content: space-around;
    padding: 16px;
    box-shadow: 0px 1px -2px 3px black;
    height: 100px;
    
}

.nav-logo{

    display: flex;
    align-items: center;
     gap: 10px; /*gap between each element in the div */
}

.nav-logo p{
    color: #171717;
    font-size: 32px;
    font-weight: 600;
}

.nav-menu{

    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px;
    color: #626262;
    font-size: 20px;
    font-weight: 500;
}

.nav-menu li{

    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 3px;
    cursor: pointer;
}

.nav-menu hr{

    border: none;
    width: 80%;
    height: 3px;
    border-radius: 10px;
    background-color: #FF4141;
}

.nav-login-cart{

    display: flex;
    align-items: center;
    gap: 45px;
}

.nav-login-cart button{
 
    width: 152px;
    height: 52px;
    outline: none;
    border: 1px solid #7a7a7a;
    border-radius: 75px;
    color: #515151;
    font-size: 18px;
    font-weight: 500;
    background-color: white;
    cursor: pointer;
    
}

.nav-login-cart button:active{
     /* this css gets activated when button is clicked */
    background-color: #f3f3f3;
}

.nav-cart-count{

       width: 23px;
       height: 23px;
       display: flex;
       align-items: center;
       justify-content: center;
       margin-top: -35px;
       margin-left: -55px;
       border-radius: 11px;
       font-size: 14px;
       color: white;
       background-color: red;
}